// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchUser } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 20,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 接收的用户们
      users: [],
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 查到的用户数量
      userprofileCount: 0,
      // html渲染的用户名
      vHtmlUserName: '',
      // 是否显示v-html嵌入的用户名关键字html标签
      isVHtmlUserName: 0
    };
  },

  mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索用户

    if (sessionStorage.getItem('searchKey')) this.getSearchUser();
  },

  methods: {
    // 搜索用户
    async getSearchUser() {
      let res = await getSearchUser(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索用户', res);

      this.userprofileCount = res.data.result.userprofileCount;
      this.users = res.data.result.userprofiles;
      if (this.users.length !== 0) this.isLoading = 0; // console.log('this.users', this.users);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新搜索用户

      this.getSearchUser();
    },

    // 用户名关键词高亮显示
    activeUserName(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlUserName = 0;
        return string;
      } else {
        this.isVHtmlUserName = 1;
        this.vHtmlUserName = string;
        return '';
      }
    },

    // 去往个人主页
    gotoPersonalHome(index) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: this.users[index].userId
        }
      });
    }

  }
};